import React, { useEffect, useState } from "react";
import "../index.css";
import Sound from "react-sound";
import bgMusic from "../Audio/Kino_Атаман.mp3";
import { Icon } from "react-icons-kit";
import { home } from "react-icons-kit/fa/home";
import { user } from "react-icons-kit/fa/user";
import { idCard } from "react-icons-kit/fa/idCard";
import { suitcase } from "react-icons-kit/entypo/suitcase";
import { plane } from "react-icons-kit/entypo/plane";
import { Outlet, Link } from "react-router-dom";
import logo from "../Images/logo.png";
import "../Responsive2.css";

const navItem = [
  {
    to: "خانه",
    icon: home,
    link: "/",
  },
  {
    icon: user,
    to: "درباره من",
    link: "/about",
  },
  {
    icon: idCard,
    to: "رزومه",
    link: "/resume",
  },
  {
    icon: suitcase,
    to: "نمونه کار",
    link: "/portfolio",
  },
  {
    icon: plane,
    to: "تماس",
    link: "/contact",
  },
];
const NavbarTwo = ({}) => {
  const [isPlaying, setIsPlaying] = useState(true);
  return (
    <div className="navbar navbar2">
      <div className="logo">
        <img src={logo} alt="" />
      </div>
      <ul className="list">
        {navItem.map((item, index) => {
          return (
            <Link to={item.link}>
              <li className="list-item" key={index}>
                <Icon className="item-logo" icon={item.icon} size={30} />
                <h4 className="item-badge">{item.to}</h4>
              </li>
            </Link>
          );
        })}
      </ul>
      <div className="player-handle" onClick={() => setIsPlaying(!isPlaying)}>
        <div
          className="player"
          style={
            !isPlaying
              ? { height: "5px", overflow: "hidden" }
              : { height: "50px" }
          }
        >
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
  );
};

export default NavbarTwo;
