import { Icon } from "react-icons-kit";
import { ic_camera_alt } from "react-icons-kit/md/ic_camera_alt";
import { ic_games } from "react-icons-kit/md/ic_games";
import { ic_drive_file_rename_outline } from "react-icons-kit/md/ic_drive_file_rename_outline";
import { motion } from "framer-motion";
import React from "react";
import mySelf from "../Images/mySelf.png.jpg";
import "./componentsStyle.css";
import "./Responsive.css";
import Resume from "../Resume/ehsan_nazeri.pdf";
const favorite = [
  {
    text: "عکاسی",
    icon: ic_camera_alt,
  },
  {
    text: "بازی",
    icon: ic_games,
  },
  {
    text: "طراحی",
    icon: ic_drive_file_rename_outline,
  },
];
const About = () => {
  return (
    <motion.div
      initial={{ y: 500, opacity: 1 }}
      animate={{
        y: 0,
        opacity: 1,
        transition: { duration: 0.5, type: "spring" },
      }}
      exit={{
        opacity: 1,
        y: -window.innerHeight,
        transition: { duration: 0.5, type: "spring", ease: "easeInOut" },
      }}
      id="About-me"
    >
      <div className="container">
        <div className="about-title">
          <h1>درباره من</h1>
          <span className="badge"></span>
        </div>
        <div className="about-box">
          <div className="row-one">
            <div className="image-box">
              <img src={mySelf} alt="" />
            </div>
          </div>
          <div className="row-two">
            <h3>احسان ناظری</h3>
            <p className="loc">ایران ,کرج</p>
            <p className="intro">
              احسان ناظری هستم دانش آموز رشته شبکه ، متولد سال 1383 ساکن کرج .
              از سال 1400 فعالیت خود را در برنامه نویسی شروع کردم و به دلیل
              علاقه زیادم به طراحی سایت وارد این عرصه شدم
            </p>
            <a href={Resume} download={Resume}>
              <button>دانلود رزومه</button>
            </a>
          </div>
        </div>
        <div className="favorite-title">
          <h1>علایق من</h1>
          <span className="badge"></span>
        </div>
        <div className="favorite-box">
          {favorite.map((item) => {
            return (
              <div className="favorite-item">
                <Icon icon={item.icon} size={25} />
                <p>{item.text}</p>
              </div>
            );
          })}
        </div>
      </div>
    </motion.div>
  );
};

export default About;
