import React, { useEffect, useState } from "react";
import "../index.css";
import Sound from "react-sound";
import bgMusic from "../Audio/Kino_Атаман.mp3";
import { Icon } from "react-icons-kit";
import { home } from "react-icons-kit/fa/home";
import { user } from "react-icons-kit/fa/user";
import { idCard } from "react-icons-kit/fa/idCard";
import { suitcase } from "react-icons-kit/entypo/suitcase";
import { plane } from "react-icons-kit/entypo/plane";
import { Outlet, Link } from "react-router-dom";
import logo from "../Images/logo.png";
import "../Responsive2.css";

const navItem = [
  {
    to: "خانه",
    icon: home,
    link: "/",
  },
  {
    icon: user,
    to: "درباره من",
    link: "/about",
  },
  {
    icon: idCard,
    to: "رزومه",
    link: "/resume",
  },
  {
    icon: suitcase,
    to: "نمونه کار",
    link: "/portfolio",
  },
  {
    icon: plane,
    to: "تماس",
    link: "/contact",
  },
];
const Navbar = ({ hamburger, setHamburger }) => {
  const [widthActive, setWidthActive] = useState(true);

  const [navActive, setNavActive] = useState(false);
  console.log(window.innerWidth);

  useEffect(() => {
    if (window.innerWidth <= 600) {
      setWidthActive(true);
      setNavActive(true);
    } else {
      setWidthActive(false);
      setNavActive(false);
    }
  }, [window.innerWidth]);

  useEffect(() => {
    if (hamburger) {
      setNavActive(true);
    } else {
      setNavActive(false);
    }
  }, [navActive, hamburger]);
  return (
    <div
      className="navbar navbar1"
      style={
        navActive ? { left: "-90px", overflow: "hidden" } : { left: "0px" }
      }
    >
      <div className="logo">
        <img src={logo} alt="" />
      </div>
      <ul className="list">
        {navItem.map((item, index) => {
          return (
            <Link to={item.link}>
              <li
                onClick={() => {
                  setHamburger(true);
                }}
                className="list-item"
                key={index}
              >
                <Icon className="item-logo" icon={item.icon} size={30} />
                <h4 className="item-badge">{item.to}</h4>
              </li>
            </Link>
          );
        })}
      </ul>
    </div>
  );
};

export default Navbar;
