import React, { useRef } from "react";
import "./componentsStyle.css";
import emailjs from "@emailjs/browser";

import { motion } from "framer-motion";
import { Icon } from "react-icons-kit";
import { ic_markunread_mailbox_outline } from "react-icons-kit/md/ic_markunread_mailbox_outline";
import { ic_add_ic_call } from "react-icons-kit/md/ic_add_ic_call";
import { instagram } from "react-icons-kit/fa/instagram";
import { phone } from "react-icons-kit/fa/phone";
import { paperPlane } from "react-icons-kit/fa/paperPlane";
import "./Responsive.css";
const socials = [
  {
    logo: paperPlane,
    text: "brainlessorg@gmail.com",
    title: "ایمیل",
  },
  {
    logo: phone,
    text: "(+98) 937 679 5572",
    title: "تلفن",
  },
  {
    logo: instagram,
    text: "ehsan.itself",
    title: "اینستاگرام",
  },
];
const Contact = () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_zkhk2ja",
        "template_inqkr4f",
        form.current,
        "KfM-d3o6nI4j1iysJ"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  };
  return (
    <motion.div
      initial={{ y: 500, opacity: 1 }}
      animate={{
        y: 0,
        opacity: 1,
        transition: { duration: 0.5, type: "spring" },
      }}
      exit={{
        opacity: 1,
        y: -window.innerHeight,
        transition: { duration: 0.5, type: "spring", ease: "easeInOut" },
      }}
      id="contact"
    >
      <div className="container">
        <div className="contact-title-box">
          <h1>تماس</h1>
          <span className="badge"></span>
        </div>
        <form onSubmit={sendEmail} ref={form} className="contact-box">
          <div className="first-side">
            <input type="text" placeholder="نام" name="user_name" />
            <input type="email" placeholder="ایمیل" name="user_email" />
          </div>
          <textarea
            name="message"
            placeholder="متن شما "
            id=""
            cols="30"
            rows="10"
            maxLength={350}
          ></textarea>
          <button type="submit">ارسال</button>
        </form>
        <div className="info-box">
          {socials.map((item) => {
            return (
              <div className="socials-box">
                <Icon icon={item.logo} size={30} />
                <h2>{item.text}</h2>
                <p>{item.title}</p>
              </div>
            );
          })}
        </div>
      </div>
    </motion.div>
  );
};

export default Contact;
