import { motion } from "framer-motion";
import "./Responsive.css";
import React from "react";
import "./componentsStyle.css";
const percentage = [
  {
    width: "100%",
  },
  {
    width: "%95",
  },
  {
    width: "%80",
  },
  {
    width: "%90",
  },
];
const skills = [
  {
    width: "100%",
    text: "HTML5",
  },
  {
    width: "%95",
    text: "CSS3",
  },
  {
    width: "%80",
    text: "Javascript",
  },
  {
    width: "%90",
    text: "React",
  },
];
const Resume = () => {
  return (
    <motion.div
      initial={{ y: 500, opacity: 1 }}
      animate={{
        y: 0,
        opacity: 1,
        transition: { duration: 0.5, type: "spring" },
      }}
      exit={{
        opacity: 1,
        y: -window.innerHeight,
        transition: { duration: 0.5, type: "spring", ease: "easeInOut" },
      }}
      id="resume"
      className="resume"
    >
      <div className="container">
        <div className="resume-title-box">
          <h1>مهارت های برنامه نویسی</h1>
          <span className="badge"></span>
        </div>
        <div className="skillbox">
          <div className="bar-box">
            <p>{skills[0].text}</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "100%" }}></div>
            </div>
          </div>
          <div className="bar-box">
            <p>{skills[1].text}</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "95%" }}></div>
            </div>
          </div>
          <div className="bar-box">
            <p>{skills[2].text}</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "80%" }}></div>
            </div>
          </div>
          <div className="bar-box">
            <p>{skills[3].text}</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "90%" }}></div>
            </div>
          </div>
        </div>
        <div className="resume-title-box">
          <h1>مهارت های غیر برنامه نویسی</h1>
          <span className="badge"></span>
        </div>
        <div className="skillbox">
          <div className="bar-box">
            <p>photoshop</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "90%" }}></div>
            </div>
          </div>
          <div className="bar-box">
            <p>premiere pro</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "80%" }}></div>
            </div>
          </div>
          <div className="bar-box">
            <p>Figma</p>
            <div className="bar">
              <div className="inside-bar" style={{ width: "80%" }}></div>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default Resume;
